import React, { useEffect, useState } from "react";
import useGetData from "hooks/use-GetData";
import { API_PRODUCTS, SHOP_PAGE } from "config";
import { Link } from 'react-router-dom';
import Banner from 'UI/Banner';
import Title from 'components/Title';
import ProductsList from 'components/ProductsList';
import Categories from './Categories';
import Services from './Services';


import './style.scss';
export default function HomePage() {

    // Lấy dữ liệu từ api truyền cho component Produsts
    // Mặc định truyền 1 mảng 8 item null để component ProductsList biết trước số lượng để tạo skeleton
    const [products, setProducts] = useState([null, null, null, null, null, null, null, null]);
    const applyProducts = (data) => { setProducts(s => data.slice(0, 8)); }
    const { status, sendRequest } = useGetData(API_PRODUCTS, applyProducts);
    useEffect(() => {
        sendRequest();
    }, []);

    return (
        <main>
            <section className='clear--padding'>
                <div className='container hero-banner'>
                    <Banner image='url(./images/banner/1.jpg)' height={480}>
                        <p className="uppercase">New Inspiration 2020</p>
                        <h2 className="uppercase">20% off on new season</h2>
                        <div className='gap'></div>
                        <Link className="btn black" to={SHOP_PAGE}>Browse collections</Link>
                    </Banner>
                </div>
            </section>
            <section>
                <div className="container">
                    <div className="gap"></div>
                    <Title
                        title='Browse our categories'
                        subtitle='Carefully created collections'
                        align='center'
                    />
                    <div className='gap'></div>
                    <Categories />
                </div>
            </section>
            <section>
                <div className="container">
                    <Title
                        title='TOP TRENDING PRODUCTS'
                        subtitle='MADE THE HARD WAY'
                        align='left'
                    />
                    <div className='gap'></div>
                    <ProductsList items={products} status={status} column={4} preview={true} />
                </div>
            </section>
            <section>
                <div className="container">
                    <Services />
                </div>
            </section>
        </main>
    )
}